
'use client';
import { useFirebase, useCollection, useDoc, useMemoFirebase } from "@/firebase";
import { collection, doc, writeBatch } from "firebase/firestore";
import { useState, useEffect, useMemo } from "react";
import { Button } from "@/components/ui/button";
import { Card, CardContent, CardHeader, CardTitle, CardDescription } from "@/components/ui/card";
import { Loader2, ArrowLeft, Save, Shuffle, PlusCircle, MinusCircle } from "lucide-react";
import { Header } from "@/components/game/Header";
import { GameContainer } from "@/components/game/GameContainer";
import Link from "next/link";
import { CARD_DATA, CardData } from "@/lib/game-data";
import { useToast } from "@/hooks/use-toast";
import { Alert, AlertDescription, AlertTitle } from "@/components/ui/alert";

const DECK_SIZE = 15;

const CollectionCard = ({ card, onAdd, onRemove, count, inDeck }: { card: CardData, onAdd: () => void, onRemove: () => void, count: number, inDeck: boolean }) => (
    <div className="p-2 border rounded-md bg-muted/50 flex items-center justify-between">
        <div>
            <p className="font-bold">{card.name} <span className="font-normal text-muted-foreground">({count})</span></p>
            <p className="text-xs text-muted-foreground">{card.description}</p>
        </div>
        <div className="flex gap-2">
             <Button size="icon" variant="ghost" onClick={onAdd} disabled={inDeck}>
                <PlusCircle className="h-5 w-5 text-green-500" />
            </Button>
            <Button size="icon" variant="ghost" onClick={onRemove} disabled={!inDeck}>
                <MinusCircle className="h-5 w-5 text-red-500" />
            </Button>
        </div>
    </div>
);


export default function DeckManagerPage({ params }: { params: { characterId: string } }) {
    const { firestore, user } = useFirebase();
    const { toast } = useToast();
    const { characterId } = params;

    const deckRef = useMemoFirebase(() => {
        if (!firestore || !user) return null;
        return doc(firestore, `users/${user.uid}/characters/${characterId}/decks/main`);
    }, [firestore, user, characterId]);

    const inventoryRef = useMemoFirebase(() => {
        if (!firestore || !user) return null;
        return collection(firestore, `users/${user.uid}/characters/${characterId}/inventory`);
    }, [firestore, user, characterId]);

    const { data: initialDeckDoc, isLoading: isDeckLoading } = useDoc<{ cards: string[] }>(deckRef);
    const { data: inventoryItems, isLoading: isInventoryLoading } = useCollection<{ name: string, type: string }>(inventoryRef);
    
    const [deck, setDeck] = useState<string[]>([]);
    const [isSaving, setIsSaving] = useState(false);
    
    const allCardsCollection = useMemo(() => {
        if (!inventoryItems || !initialDeckDoc) return {};
        const collection: Record<string, number> = {};

        // Aggregate all cards from inventory and the initial deck
        const allCardNames = [...initialDeckDoc.cards, ...inventoryItems.filter(item => item.type === 'card').map(item => item.name)];
        
        allCardNames.forEach(cardName => {
            if (CARD_DATA[cardName]) {
                 collection[cardName] = (collection[cardName] || 0) + 1;
            }
        });
        return collection;
    }, [inventoryItems, initialDeckDoc]);
    
    useEffect(() => {
        if (initialDeckDoc) {
            setDeck(initialDeckDoc.cards);
        }
    }, [initialDeckDoc]);


    const handleAddToDeck = (cardName: string) => {
        if (deck.length >= DECK_SIZE) {
            toast({ title: "Deck is full!", description: "You must remove a card before adding another.", variant: "destructive"});
            return;
        }
        
        const totalInCollection = allCardsCollection[cardName] || 0;
        const currentlyInDeck = deck.filter(c => c === cardName).length;
        if(currentlyInDeck < totalInCollection) {
            setDeck(prev => [...prev, cardName]);
        } else {
            toast({ title: "No more copies!", description: `You don't have any more copies of ${cardName} to add.`, variant: "destructive"});
        }
    };
    
    const handleRemoveFromDeck = (cardName: string) => {
        setDeck(prev => {
            const index = prev.lastIndexOf(cardName);
            if(index > -1) {
                const newDeck = [...prev];
                newDeck.splice(index, 1);
                return newDeck;
            }
            return prev;
        });
    };

    const handleSaveChanges = async () => {
        if (!deckRef) return;
        if (deck.length !== DECK_SIZE) {
             toast({ title: "Invalid Deck Size", description: `Your deck must contain exactly ${DECK_SIZE} cards.`, variant: "destructive"});
             return;
        }
        setIsSaving(true);
        try {
            const batch = writeBatch(firestore);
            batch.update(deckRef, { cards: deck });
            await batch.commit();
            toast({ title: "Deck Saved!", description: "Your deck has been updated for your next battle."});
        } catch (e: any) {
            toast({ title: "Error Saving Deck", description: e.message, variant: "destructive" });
        } finally {
            setIsSaving(false);
        }
    };

    const isLoading = isDeckLoading || isInventoryLoading;

    if (isLoading) {
        return (
            <div className="flex justify-center items-center min-h-screen">
                <Loader2 className="h-16 w-16 animate-spin" />
            </div>
        );
    }
    
    const sortedCollection = Object.keys(allCardsCollection).sort((a,b) => a.localeCompare(b));

    return (
        <>
            <Header />
            <main className="py-12">
                <GameContainer>
                     <div className="mb-4">
                        <Button asChild variant="outline">
                            <Link href={`/adventure/${characterId}`}>
                                <ArrowLeft className="mr-2 h-4 w-4" />
                                Back to Adventure
                            </Link>
                        </Button>
                    </div>

                    <div className="grid grid-cols-1 lg:grid-cols-2 gap-8">
                        {/* Your Collection */}
                        <Card>
                            <CardHeader>
                                <CardTitle>Your Card Collection</CardTitle>
                                <CardDescription>All the cards you own. Add them to your deck to use them in battle.</CardDescription>
                            </CardHeader>
                            <CardContent className="space-y-2 h-[60vh] overflow-y-auto">
                                {sortedCollection.length > 0 ? sortedCollection.map(cardName => (
                                    <CollectionCard 
                                        key={cardName}
                                        card={CARD_DATA[cardName]}
                                        count={allCardsCollection[cardName]}
                                        onAdd={() => handleAddToDeck(cardName)}
                                        onRemove={() => handleRemoveFromDeck(cardName)}
                                        inDeck={deck.filter(c => c === cardName).length > 0}
                                    />
                                )) : <p className="text-muted-foreground">Your collection is empty.</p>}
                            </CardContent>
                        </Card>

                        {/* Current Deck */}
                        <Card>
                            <CardHeader>
                                <CardTitle>Battle Deck ({deck.length}/{DECK_SIZE})</CardTitle>
                                <CardDescription>This is the deck you will bring into your next fight.</CardDescription>
                            </CardHeader>
                            <CardContent className="space-y-2 h-[60vh] overflow-y-auto">
                                {deck.length > 0 ? deck.map((cardName, index) => (
                                    <div key={index} className="p-2 border rounded-md bg-secondary/30 flex items-center justify-between">
                                        <p>{cardName}</p>
                                        <Button size="icon" variant="ghost" onClick={() => handleRemoveFromDeck(cardName)}>
                                            <MinusCircle className="h-5 w-5 text-red-500" />
                                        </Button>
                                    </div>
                                )) : (
                                    <div className="text-center text-muted-foreground py-16">
                                        <p>Your deck is empty.</p>
                                        <p>Add cards from your collection.</p>
                                    </div>
                                )}
                                 {deck.length !== DECK_SIZE && (
                                    <Alert variant="destructive">
                                        <AlertTitle>Invalid Deck Size!</AlertTitle>
                                        <AlertDescription>Your deck must have exactly {DE_CK_SIZE} cards to be valid.</AlertDescription>
                                    </Alert>
                                )}
                            </CardContent>
                            <CardContent>
                                 <Button onClick={handleSaveChanges} disabled={isSaving || deck.length !== DECK_SIZE} className="w-full">
                                    {isSaving ? <Loader2 className="mr-2 h-4 w-4 animate-spin"/> : <Save className="mr-2 h-4 w-4" />}
                                     Save Deck
                                 </Button>
                            </CardContent>
                        </Card>
                    </div>

                </GameContainer>
            </main>
        </>
    );
}

